import React from 'react';
import moment from 'moment';
import 'moment/locale/es';

const aoActual = moment().format('YYYY');

function Ao() {
    return (
      <div className='text-white'>
        <p className='text-white m-0'> Proyecto { aoActual }</p>
      </div>
      
    );
    
}

export default Ao; 